body {
    background-color: #F5F5F5;
}

nav {
    background-color: white;
    padding: 10px 30px; 
    display: flex; 
    align-items: center; 
    justify-content: space-between;
    overflow: hidden; 
}

.navbar-logo {
    max-height: 60px; 
    width: auto; 
}

nav ul {
    list-style: none;
    display: flex;
    gap: 50px;
    margin: 0;
    padding: 0;
    justify-content: flex-end; 
    margin-right: 40px;
}

nav ul li {
    display: inline;
}

nav ul li a {
    color: black;
    text-decoration: none;
    font-weight: bold;
    position: relative; 
    padding: 5px 10px;
    transition: color 0.3s ease, box-shadow 0.3s ease;
}

nav ul li a::after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: 2px;
    background-color: transparent; 
}

nav ul li a:hover,
nav ul li a:focus {
    color: #007bff; 
    box-shadow: 0px 0px 8px 2px rgba(0, 123, 255, 0.75); 
    border-radius: 4px; 
}

nav ul li a:active {
    color: #0056b3; 
    box-shadow: 0px 0px 12px 3px rgba(0, 86, 179, 0.75); 
}

.quesBox {
   border: 2px rgb(165, 161, 161) solid;
   padding-top: 1em;
   padding-bottom: 1em;
   padding-left: 1em;
}